'use client';

import { useState, useEffect } from 'react';
import { useAuth } from '../contexts/AuthContext';

type Device = {
  device_id: string;
  device_name: string;
  device_type: string;
  online: boolean;
  battery_level: number | null;
  last_seen: string;
  ip_address: string;
  mac_address: string | null;
  device_uuid: string;
  user_id: number;
};

const Button = ({ children, onClick, className = '', ...props }: any) => (
  <button
    onClick={onClick}
    className={`px-4 py-2 rounded-md bg-blue-600 text-white hover:bg-blue-700 transition-colors ${className}`}
    {...props}
  >
    {children}
  </button>
);

export default function DeviceList() {
  const [devices, setDevices] = useState<Device[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const { user, isAuthenticated } = useAuth();

  useEffect(() => {
    const fetchDevices = async () => {
      if (!isAuthenticated) {
        setError('Please log in to view devices');
        setIsLoading(false);
        return;
      }

      try {
        const token = user?.token;
        if (!token) {
          throw new Error('No authentication token found. Please log in again.');
        }

        const apiUrl = 'https://web-production-d7d37.up.railway.app/device/list?include_offline=true';
        
        const response = await fetch(apiUrl, {
          method: 'GET',
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json',
            'Accept': 'application/json',
          },
          credentials: 'include',
        });

        if (!response.ok) {
          if (response.status === 401) {
            // Token might be expired, clear auth and redirect to login
            localStorage.removeItem('auth_token');
            localStorage.removeItem('user');
            window.location.href = '/auth/login';
            return;
          }
          
          const errorData = await response.json().catch(() => ({}));
          throw new Error(errorData.detail || `HTTP error! Status: ${response.status}`);
        }

        const responseData = await response.json();
        
        if (responseData && Array.isArray(responseData.devices)) {
          setDevices(responseData.devices);
        } else {
          throw new Error('Invalid response format from server');
        }
      } catch (err) {
        const errorMessage = err instanceof Error ? err.message : 'Failed to fetch devices';
        console.error('Error fetching devices:', errorMessage, err);
        setError(`Failed to load devices: ${errorMessage}`);
        setDevices([]);
      } finally {
        setIsLoading(false);
      }
    };

    fetchDevices();
  }, []);

  if (isLoading) {
    return (
      <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '256px' }}>
        <div style={{ 
          width: '48px', 
          height: '48px', 
          border: '3px solid #f3f3f3',
          borderTop: '3px solid #3498db',
          borderRadius: '50%',
          animation: 'spin 1s linear infinite',
          margin: '0 auto'
        }}></div>
        <style jsx>{`
          @keyframes spin {
            0% { transform: rotate(0deg); }
            100% { transform: rotate(360deg); }
          }
        `}</style>
      </div>
    );
  }

  if (error) {
    return (
      <div style={{
        backgroundColor: '#fee2e2',
        borderLeft: '4px solid #ef4444',
        color: '#b91c1c',
        padding: '1rem',
        marginBottom: '1rem',
        borderRadius: '0.25rem'
      }}>
        <p>{error}</p>
      </div>
    );
  }

  return (
    <div style={{ marginTop: '1rem' }}>
      {devices.length === 0 ? (
        <div style={{ textAlign: 'center', padding: '3rem 0' }}>
          <p style={{ color: '#6b7280', marginBottom: '1rem' }}>No devices found. Connect a device to get started.</p>
          <Button>Add Device</Button>
        </div>
      ) : (
        <div style={{
          gridTemplateColumns: 'repeat(auto-fill, minmax(300px, 1fr))',
          gap: '1rem',
          marginTop: '1rem'
        }}>
          {devices.map((device) => {
            const getStatusInfo = (device: Device) => {
              if (!device.online) {
                return { bg: 'bg-gray-100 text-gray-800', text: 'Offline' };
              }
              return { bg: 'bg-green-100 text-green-800', text: 'Online' };
            };
            
            return (
              <div 
                key={device.device_id}
                className="flex items-center justify-between p-4 bg-white rounded-lg shadow hover:shadow-md transition-shadow"
              >
                <div className="flex items-center space-x-4">
                  <div className="p-3 rounded-full bg-blue-50">
                    <svg
                      className="w-6 h-6 text-blue-600"
                      fill="none"
                      stroke="currentColor"
                      viewBox="0 0 24 24"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth={2}
                        d="M9.75 17L9 20l-1 1h8l-1-1-.75-3M3 13h18M5 17h14a2 2 0 002-2V5a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z"
                      />
                    </svg>
                  </div>
                  <div>
                    <h3 className="font-medium text-gray-900">{device.device_name}</h3>
                    <div className="flex items-center mt-1">
                      <span
                        className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${getStatusInfo(device).bg}`}
                      >
                        {getStatusInfo(device).text}
                      </span>
                      {device.battery_level !== null && (
                        <span className="ml-2 text-sm text-gray-500">
                          {device.battery_level}% battery
                        </span>
                      )}
                    </div>
                  </div>
                </div>
                <div className="flex space-x-2">
                  <button 
                    className="px-3 py-1 text-sm text-gray-700 border border-gray-300 rounded hover:bg-gray-50 transition-colors"
                    onClick={() => {/* Handle view details */}}
                  >
                    View Details
                  </button>
                  <button 
                    className="px-3 py-1 text-sm text-gray-700 border border-gray-300 rounded hover:bg-gray-50 transition-colors"
                    onClick={() => {/* Handle settings */}}
                  >
                    Settings
                  </button>
                </div>
              </div>
            );
          })}
        </div>
      )}
    </div>
  );
}
